<template>
  <xdh-map-html v-bind="$props">
    <i :class="icon" v-on="$listeners"></i>
  </xdh-map-html>
</template>

<script>
  /**
   * Icon
   * @module xdh-map-icon
   *
   */
  import XdhMapHtml from '../../html'

  export default {
    name: 'XdhMapIcon',
    components: {
      XdhMapHtml
    },
    /**
     * 
     * @member props
     * @property {string} icon 
     * @property {string} [positioning]  center-center
     * @property {number[]} position 
     * @property {number[]} [offset] 
     * @property {boolean} [stopEvent]  true
     */
    props: {
      ...XdhMapHtml.props,
      icon: String
    }
  }
</script>
