import {prefix, mix} from '../util';

/**
 *
 * @property {number} [strokeWidth] 
 * @property {string} [strokeColor] 
 * @property {string} [strokeLineCap] 
 * @property {string} [strokeLineJoin] 
 * @property {number[]} [strokeLineDash] 
 * @property {number} [strokeLineDashOffset]
 * @property {number} [strokeMiterLimit]
 *
 */

const options = {
  // 
  color: {
    type: String,
    default: 'transparent'
  },
  lineCap: {
    type: String,
    default: 'round'
  },
  lineJoin: {
    type: String,
    default: 'round'
  },
  lineDash: Array,
  lineDashOffset: Number,
  miterLimit: Number,
  width: Number
}

const prefixOptions = prefix('stroke', options)
export default {
  props: {
    ...prefixOptions
  },
  methods: {
    strokeRender() {
      return {
        className: 'Stroke',
        ...mix(this, 'stroke', options)
      }
    }
  }
}
