// 
export function validate(key) {
  const keys = Object.keys(LAYERS)
  const valid = keys.includes(key)
  if (!valid) {
    throw new Error(` ${key} `)
  }
  return valid
}


/**
 * 
 * @param {Object} layers 
 *
 * @example
 *
 * setLayersConfig({
 *  Baidu: {
 *    title:''
 *  }
 * })
 *
 */
export function set(layers) {
  Object.keys(layers).forEach(key => {
    const layer = layers[key]
    Object.assign(LAYERS[key], layer)
  })
}

/**
 * 
 * @param {String} [key=null] ,null
 * @param {boolean} [toArray=false] 
 * @return {Object|Array}
 */
export function get(key = null, toArray = false) {
  if (key && validate(key)) {
    return LAYERS[key]
  }
  if (!toArray) {
    return LAYERS
  }
  return Object.keys(LAYERS).map(key => {
    return {
      name: key,
      ...LAYERS[key]
    }
  })
}
