<template>
  <div></div>
</template>

<script>

  /**
   * 
   * @module xdh-map-heat
   */

  import Feature from 'ol/Feature'
  import Point from 'ol/geom/Point'
  import Vector from 'ol/source/Vector'
  import Heatmap from 'ol/layer/Heatmap'
  import CleanMixin from 'utils/mixins/clean'
  import factory from 'utils/mixins/factory'
  import {getParent, mapReady} from 'utils/util'
import { convertToWgs84 } from 'utils/convert'
  /**
   * 
   * @member props
   * @property {number} [opacity=1] 
   * @property {boolean} [visible=true] 
   * @property {number} [zIndex] ,z-indexZ-index
   * @property {number} [minResolution]  
   * @property {number} [maxResolution] 
   * @property {string[]} [gradient] CSS
   * @property {number}  [radius=8] 
   * @property {number} [blur=15] 
   * @property {number} [shadow=250] 
   * @property {string|Function} [weight=weight] 01
   * @property {object[]} data lonlatweight
   * @property {object} [prop] 
   */

  const options = {
    // 0,1
    opacity: {
      type: Number,
      default: 1
    },
    // 
    visible: {
      type: Boolean,
      default: true
    },
    // z-indexZ-index
    zIndex: Number,
    // 
    minResolution: Number,
    // 
    maxResolution: Number,
    // CSS
    gradient: {
      type: Array,
      default() {
        return ['#00f', '#0ff', '#0f0', '#ff0', '#f00']
      }
    },
    // 
    radius: {
      type: Number,
      default: 8
    },
    // 
    blur: {
      type: Number,
      default: 15
    },
    // 
    shadow: {
      type: Number,
      default: 250
    },
    // 01
    weight: {
      type: [String, Function],
      default: 'weight'
    }
  }

  const factoryMixin = factory(options)

  export default {
    name: 'XdhMapHeat',
    mixins: [CleanMixin, factoryMixin],
    props: {
      ...factoryMixin.props,
      data: {
        type: Array,
        default() {
          return []
        }
      },
      prop: {
        type: Object,
        default() {
          return {
            lon: 'lon',
            lat: 'lat',
            //  0 ~ 1
            weight: 'weight'
          }
        }
      }
    },
    watch: {
      data() {
        this.removeLayer()
        this.draw()
      }
    },
    inject: ['coordType'],
    methods: {
      draw() {
        if (!this.parent) return
        const features = this.createFeatures()
        const source = new Vector()
        source.addFeatures(features)
        this.layer = new Heatmap({
          source: source,
          ...this.styleFactory()
        })
        this.parent.map.addLayer(this.layer)
      },
      createFeatures() {
        const prop = this.prop
        return this.data.map(item => {
          return new Feature({
            // 
            geometry: new Point(convertToWgs84(this.coordType, [item[prop.lon], item[prop.lat]])),
            weight: item[prop.weight] || 1
          })
        })
      },
      removeLayer() {
        if (this.parent && this.layer) {
          this.layer.disposeInternal()
          this.parent.map.removeLayer(this.layer)
        }
      }
    },
    created() {
      this.parent = getParent.call(this)
      mapReady.call(this, this.draw)

    },
    beforeDestroy() {
      this.removeLayer()
    }
  }
</script>

