<template>
  <xdh-map-html v-bind="$props"
                ref="html"
                positioning="bottom-center"
                :class="classes"
                :style="{'width': width}">
    <i class="xdh-map-popup__close" v-if="closable" @click="hide"></i>
    <div v-if="title" class="xdh-map-popup__title">
      <slot name="title">
        <i :class="icon"></i> {{typeof title === 'String' ? title : ''}}
      </slot>
    </div>
    <div class="xdh-map-popup__body" :style="{'height': height}">
      <slot></slot>
    </div>
  </xdh-map-html>
</template>

<script>
  /**
   * 
   * @module xdh-map-popup
   */

  import XdhMapHtml from '../../html'
  import {getParent, mapReady} from 'utils/util'

  /**
   * 
   * @member slots
   * @property {string} title 
   * @property {string} default 
   */
  export default {
    name: 'XdhMapPopup',
    components: {
      XdhMapHtml
    },
    /**
     * 
     * @member props
     * @property {string} [width] 
     * @property {string} [height] 
     * @property {string} [title] 
     * @property {string} [icon] 
     * @property {boolean} [closable=true] 
     * @property {boolean} [closeOnClick=true] 
     * @property {string} [theme=light]  'dark', 'light'
     *
     */
    props: {
      ...XdhMapHtml.props,
      width: String,
      height: String,
      title: {
        type: String || Boolean
      },
      icon: {
        type: String
      },
      closable: {
        type: Boolean,
        default: true
      },
      closeOnClick: {
        type: Boolean,
        default: true
      },
      showErrow: {
        type: Boolean,
        default: true
      },
      theme: {
        type: String,
        default: 'light',
        validator() {
          return ['dark', 'light']
        }
      }
    },
    computed: {
      classes() {
        return ['xdh-map-popup', `is-${this.theme}`, `${this.showErrow ? 'errow' : ''}`]
      },
      styles() {
        return {
          width: this.width,
          height: this.height
        }
      }
    },
    methods: {
      show(position) {
        this.$refs.html.show(position || this.position)
      },
      hide() {
        this.$refs.html.hide()
        this.$emit('on-closed')
      },
      ready(map) {
        if (this.closeOnClick) {
          map.on('click', this.hide)
        }
      }
    },
    created() {
      this.parent = getParent.call(this)
      mapReady.call(this, this.ready)
    },
    beforeDestroy() {
      if (this.closeOnClick) {
        this.parent.map.un('click', this.hide)
      }
    }
  }
</script>
