<template>
  <div class="xdh-map-placement"
       :class="classes"
       :style="styles">
    <slot></slot>
  </div>
</template>

<script>
  /**
   * 
   * @module xdh-map-placement
   */
  export default {
    name: 'XdhMapPlacement',
    /**
     * 
     * @member props
     * @property {string} [placement] 'left-top', 'center-top', 'right-top''left-center', 'center-center', 'right-center''left-bottom', 'center-bottom', 'right-bottom'
     * @property {number} [zIndex=1] 
     * @property {number[]} [margin] 
     * @property {string[]} [theme]   'default', 'light', 'dark'
     */
    props: {
      // 
      placement: {
        type: String,
        default: 'left-bottom',
        validator(val) {
          return [
            'left-top', 'center-top', 'right-top',
            'left-center', 'center-center', 'right-center',
            'left-bottom', 'center-bottom', 'right-bottom'
          ].includes(val)
        }
      },
      // 
      zIndex: {
        type: Number,
        default: 1
      },
      // 
      margin: {
        type: Array,
        default() {
          return [0, 0, 0, 0]
        }
      },
      // 
      theme: {
        type: String,
        default: '',
        validator() {
          return ['default', 'light', 'dark']
        }
      }
    },
    computed: {
      styles() {
        return {
          zIndex: this.zIndex,
          margin: this.margin.map(n => `${n}px`).join(' ')
        }
      },
      classes() {
        return [`is-${this.theme}`, this.placement]
      }
    }
  }
</script>

